    async created() {
        var vm = this;
        await vm.GetTemplateCategoryListForCA();
        await vm.GetStatusList();
        if(vm.EditItme.template_id>0){        
            (vm.FormSchema)[0].Data[0].value=vm.EditItme.template_name;
            (vm.FormSchema)[0].Data[0].disabled= true;
            (vm.FormSchema)[0].Data[1].value=vm.EditItme.template_subject;            
            vm.CatData.forEach(option => {               
                if (option.template_category_name === vm.EditItme.template_category_id) {                                            
                    (vm.FormSchema)[0].Data[2].value = option.module_id;                                                                                
                }
            });
            await vm.GetSubCategoryDDL((vm.FormSchema)[0].Data[2].value);          
            vm.SubCatData.forEach(option => {               
                if (option.NAME === vm.EditItme.template_sub_category_id) {               
                    (vm.FormSchema)[0].Data[3].value = option.SUB_MODULE_ID;                    
                }
            });            
            (vm.FormSchema)[0].Data[4].value = vm.EditItme.status_id.value; 
             vm.editorData=vm.EditItme.template_content;                       
        }
