    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        async GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetStatusList().then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    vm.SubCatData.forEach((itm, index) => {
                        if (itm.STATUS != 'Deleted') {
                            (vm.FormSchema)[0].Data[4].config.options.push({
                                name: itm.STATUS,
                                value: itm.STATUS_ID
                            })
                        }
                    })
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        //get sub category ddl list by module id
        async GetSubCategoryDDL(evt) {           
            var vm = this;
            vm.isLoading = true;
            if (evt.target == undefined) {
                var url = `ModuleID=${evt}`
                vm.id = evt;
            }
            else {
                var url = `ModuleID=${evt.target.value}`
                vm.id = evt.target.value;
            }
            await DataService.GetTemplateSubCategoryList(url).then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    (vm.FormSchema)[0].Data[3].config.options = [];
                    vm.SubCatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[3].config.options.push({
                            name: itm.NAME,
                            value: itm.SUB_MODULE_ID
                        })
                    })                    
                    vm.isLoading = false;
                }
            });
            await vm.SetTemplateVariable(vm.id);
        },
        //get category ddl list
        async GetTemplateCategoryListForCA() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetTemplateCategoryListForCA().then((response) => {
                if (JSON.parse(response.data) != null) {
                    // var CatData = [];
                    vm.CatData = JSON.parse(response.data);
                    vm.CatData.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[2].config.options.push({
                            name: itm.template_category_name,
                            value: itm.module_id,
                        })
                    })
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        BindFields(itm) {
            // var vm = this;
            const editor = this.$refs.textarea.editor;
            // const selection = editor.model.document.selection;
            const selection = this.$refs.textarea.$_instance.model.document.selection;
            if (selection) {
                const position = selection.getFirstPosition();
                this.currentCursorPosition = {
                    index: position.index,
                    // path: position.path,
                };
                this.currentCursorPosition.index=itm;
                this.editorData+= '@'+itm+'@'
            } 
            else {
                console.log('No selection found');
            }
            // this.$refs.textarea.$_instance.model.document.selection._selection
        },
        SubmitData() {
            debugger;
            var vm = this;
            // vm.$refs.AddEmailTemplate.validate().then(result => {
            //     if (result) {
                    var Template_Name = vm.FormSchema[0].Data[0].value;
                    var Template_Subject = vm.FormSchema[0].Data[1].value;
                    var moduleId = vm.FormSchema[0].Data[2].value;
                    var Template_SubCategory_Id = vm.FormSchema[0].Data[3].value;
                    var Status_Id = vm.FormSchema[0].Data[4].value;
                    var foundObject = vm.CatData.find(x => x.module_id == moduleId);
                    if (foundObject) {
                        var Template_Category_Id = foundObject.template_category_id;
                    }
                   // vm.$refs.textarea.editor.exportHtml((data) => {
                        //var json = data.design; //get the content of form design.
                        var html = vm.$refs.textarea.$_lastEditorData;  //get the html of template which is created.
                        //var content = JSON.stringify(json);

                        var objData = {
                            TemplateId: (vm.EditItme.template_id > 0 ? vm.EditItme.template_id:vm.TemplateId),
                            TemplateName: Template_Name,
                            TemplateContent: html,
                            StatusId: Status_Id,
                            TemplateCategoryId: Template_Category_Id,
                            TemplateSubCategoryId: Template_SubCategory_Id,
                            TemplateSubject: Template_Subject,
                            TemplateJson: "",
                            TemplateType: true
                        }
                        var formData = new FormData();
                        var postString = {"postString": objData}
                        formData.append("postString", JSON.stringify(objData));
                        // var url= `TemplateId=${vm.TemplateId}&TemplateName=${Template_Name}&TemplateContent=${html}&StatusId=${Status_Id}&TemplateCategoryId=${Template_Category_Id}&TemplateSubCategoryId=${Template_SubCategory_Id}&TemplateSubject=${Template_Subject}&TemplateJson=${content}&TemplateType=true`
                        var param = `objTemp=${JSON.stringify(objData)}`;
                        DataService.AddAdvancedEmailTemplate(formData).then((response) => {  
                            debugger;                         
                            const Data=JSON.parse(response.data); 
                             if (Data[0].status == '' && Data[0].msg == "TemplateNameExists") {
                                vm.ShowAlert(vm.$t("TemplateNameExists"), "warning", vm.$t('Alert'));
                              }
                            else {
                               
                                if (Data[0].status_id==1 && Data[0].status_msg == 'add') {
                                    vm.ShowAlert(vm.$t("AddEmailSuccess"), "success", vm.$t('Alert'));
                                    window.location.reload();
                                }
                                else if (Data[0].status_id==2 && Data[0].status_msg == 'updated') {
                                    vm.ShowAlert(vm.$t("UpdateEmail"), "success", vm.$t('Alert'));
                                     window.location.reload();
                                }
                                else {
                                    vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                                }
                            }                            
                        })
                    //});

            //     }
            // })
        },
        SetTemplateVariable(moduleId) {
            var vm = this;
            vm.isLoading = true;
            var foundObject = vm.CatData.find(x => x.module_id == moduleId);
            if (foundObject) {
                vm.UserGuidData=[];
                var template_category_name = foundObject.template_category_name;
                if (template_category_name == 'Appointment Calendar') {
                    vm.UserGuidData=vm.appointmentuserguide;
                }
                else if (template_category_name == 'Appointment Registration') {
                    vm.UserGuidData=vm.appointmentregistrationuserguide;
                }
                else if (template_category_name == 'Greeting') {
                    vm.UserGuidData=vm.greetinguserguide;
                }
                else if (template_category_name == 'Ticketing') {
                    vm.UserGuidData=vm.ticketinguserguide;
                }
                else if (template_category_name == 'CRM') {
                    vm.UserGuidData=[];
                }
                else if (template_category_name == 'EVENT') {
                    vm.UserGuidData=[];
                }
                else if (template_category_name == 'FSM') {
                    vm.UserGuidData=[];
                }
                else
                {
                    vm.UserGuidData=vm.greetinguserguide;
                }    
            }
            vm.isLoading = false;
        },
