<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-2 mt-2">
                <LeftColumn />
            </div>
            <div class="col-10" v-if="isShowList">
                <tg-list :IsShowAction="false" :showCheckBox="true" ModuleName="EmailTemplate" SubModuleCode="EmailTemplate"
                    IdentityColumn="template_id" :HeaderText="$t('EmailTemplate')" :ListData="EmailTemplateData" :HeaderData="filteredHeaders"
                    :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :IsSearchFieldsOnTop="false"
                    SearchPosition="top" :SearchFields="topSearchFields"
                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                    :IsFilterApplied="searchCondition ? true : false" v-on:LeftsearchButtonClick="leftCommonSearch" :LegendArray="[]" sourcetype=""
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
                
                    <template v-slot:slotdata="{ data }">
                        <!-- {{ data.row.fv }}
                        <template v-if="data.column.COLUMN_NAME == 'template_category_id'">
                            <template  v-if="data.row.template_category_id.Name != null">
                                <div>{{ data.row.template_category_id.Name }}</div>
                            </template>
                        </template> -->
                        <template v-if="data.column.COLUMN_NAME == 'status_id'"> 
                            <listing-status :isfor="'COMPANYSETUP_UPDATE'" v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')"
                                ref="updatestatus" v-bind:dataid="data.row.template_id" data-controller="COMPANYSETUP_UPDATE"
                                v-bind:statuslist="StatusListForListing" v-bind:item="data.row.template_id" data-action="index" module="EmailTemplate" subModule="EmailTemplate"
                                v-bind:dataselected="data.row.status_id.value == 1001 ? 'Active' : 'Inactive'"
                                v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                            </listing-status>
                        </template>
                    </template>
                </tg-list>
            </div>
            <div class="col-10" v-else-if="showEmailTemplate">
                <AddEmailTemplate :EditItme="Item"></AddEmailTemplate>
                
            </div>
            <div class="col-10"  v-else-if="ShowAddAdvancedEmailTemplate" >
                <AddAdvancedEmailTemplate :EditItme="Item" :Template_id="Template_id"></AddAdvancedEmailTemplate>
            </div>
        </div>
        <!---modal popup-->
        <!-- <AddCompanySetup :recordId="recordId" :title="$t('CAManageEmailTemplate')" v-if="showEmailTemplate" v-on:close="showEmailTemplate=false;recordId=0" v-on:addReloadData="FetchData();showEmailTemplate=false;recordId=0" v-bind:moduleName="'EmailTemplate'" v-bind:emitName="'addReloadData'" v-bind:addApiUrl="'/EmailTemplate/add'" v-bind:subModule="'EmailTemplate'" /> -->
    </div>
</template>
<script>
import EmailTemplateDataService from "@/services/DataService";
import Schema from "@/views/Company/EmailTemplate/EmailTemplateSchema";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
import AddAdvancedEmailTemplate from "@/components/Modals/Addnew.vue"
import AddEmailTemplate from '@/components/Modals/Add.vue'
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
        AddAdvancedEmailTemplate,
        AddEmailTemplate
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            recordId:0,
            showEmailTemplate:false,
            noRecord:false,
            isLoading: false,
            CompanyId: null,
            EmailTemplateData: [],
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            topSearchFields: Schema.topsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            searchcondition: '',
            MODULENAME: 'EmailTemplate',
            SUBMODULECODE: 'EmailTemplate',
            privilegeParams: null,
            sharetype: '',
            // Headers: [],
            filteredHeaders: [],
            StatusListForListing: [
                { 
                    STATUS: 'Active', 
                    STATUS_ID: 1001, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#28a745" 
                },{ 
                    STATUS: 'Inactive', 
                    STATUS_ID: 1002, 
                    STATUS_COLOR: "#ffffff", 
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
            ShowAddAdvancedEmailTemplate:false,
            Template_id:0,
            isShowList:true,
            Item:[]
        }
    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=EmailTemplate&action=`;
        await ths.FetchData();
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
        ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
            const filteredItem = { ...item };

            // Check if USER_EMP_ID is null or empty, then replace with '--'
            if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                filteredItem.USER_EMP_ID = '--';
            }

            return filteredItem;
            });
        },
        
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        renderActions: function (row) {
            console.log(row);
            // var self = this;
            // let rtnActionArray = [];
            // self.listActions.forEach(actionItem => {
            //     let action = { ...actionItem };
            //     switch (action.title) {
            //         case 'View':
            //             action.isVisible = row.ACCESS != null ? true : true;
            //             break;
            //         case 'Send Email':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Schedule':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Share':
            //             action.isVisible = row.ACCESS != 1 && row.ACCESS != 2 ? true : false;
            //             break;
            //         case 'Edit':
            //             action.isVisible = row.ACCESS != 1 ? true : false;
            //             break;
            //         default:
            //             action.isVisible = true;
            //             break;
            //     }
            //     rtnActionArray.push(action);
            // });
            // return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {
            console.log(event)
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ActiveMultipleListing":
                    this.UpdateStatus("Active");
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus("Inactive");
                    break;
                case "AddEmailTemplate":
                    this.AddEmailTemplate();
                    break;
                case "AddAdvancedEmailTemplate":
                    this.AddAdvancedEmailTemplate();
                    break;
            }
        },
        AddEmailTemplate()
        {
            var vm=this;
            vm.ShowAddAdvancedEmailTemplate=false;
            vm.isShowList=false;
            vm.showEmailTemplate = true;

        },
        AddAdvancedEmailTemplate(){
            var vm=this;
            vm.showEmailTemplate=false;
            vm.isShowList=false;            
            vm.ShowAddAdvancedEmailTemplate=true;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'sharereport':
                    var vm = this;
                    EmailTemplateDataService.CheckPrivilege(vm.privilegeParams+'ShareReport').then((response)=>{
                        if(response.data){
                            vm.sharetype = callbackfunction
                            vm.folderid = item.FOLDER_ID;
                            vm.ReportID = item.REPORT_ID;
                            vm.foldername = item.REPORTNAME;
                            vm.showsharereportview = true;
                            $("#modalshareview").modal('show');
                            setTimeout(function () {
                                $("#modalshareview").modal('show');
                                setTimeout(function () {
                                    vm.SetBootstraptoolTip();
                                }, 200);
                            }, 1000);
                        }
                        else{
                            vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                            return false;
                        }
                    });
                    break;
                case 'sendemail':
                    vm.sendemail(item.REPORT_ID, item.MODULENAME, item.SUBMODULECODE);
                    break;
                case 'editreport':
                    vm.editreport(item);
                    break;
                case 'setschedule':
                    vm.setschedule(item.REPORT_ID);
                    break;
                case 'ViewReport':
                    vm.ViewReport(item.REPORT_ID, item.ACCESS, item.FOLDER_ID);
                    break;
            }
        },
        editreport: function (data) {
            var vm = this;
            EmailTemplateDataService.CheckPrivilege(vm.privilegeParams+'Update').then((response)=>{
                if(response.data){
                    vm.$router.push({
                        name: 'EditDataReport',
                        params: {
                            Modulename: data.MODULENAME, Submodulecode: data.SUBMODULECODE, ReportName: data.REPORTNAME, ReportID: data.REPORT_ID,
                            Module_Id: data.MODULE_ID, Folder_Id: data.FOLDER_ID, Sub_Module_Id: data.Sub_Module_Id, Version: vm.Version, Action: 'Edit'
                        }
                    });
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        ViewReport: function (ReportID, ACCESS, FolderID) {
            var vm = this;
            vm.$router.push('/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ReportID, ACCESS: ACCESS , Folder_Id: FolderID })))

        },
        sortdata: async function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            await this.FetchData();
            $("#th-" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: async function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            await this.FetchData();
        },
        leftCommonSearch: async function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }

            await this.FetchData();
        },
        ManageFolder: function () {
            var vm = this;
            EmailTemplateDataService.CheckPrivilege(vm.privilegeParams+'FolderListing').then((response)=>{
                if(response.data){
                    vm.ShowManageView = true;
                    $("#modalManageView").modal('show');
                    setTimeout(function () {
                        $("#modalManageView").modal('show');
                        setTimeout(function () {
                            vm.SetBootstraptoolTip();
                        }, 200);
                    }, 1000);
                    vm.$refs.managefolder?.GetViewList();
                }
                else{
                    vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
                    return false;
                }
            });
        },
        AddDesignation: function () {
            var vm = this;
            console.log(vm, "Add");
        },
        commonDeleteData: function (event) {
            var vm = this;
            // EmailTemplateDataService.CheckPrivilege(vm.privilegeParams+'Delete').then((response)=>{
            //     if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                            var postJSON = {
                                ids: val,
                                moduleName: "EmailTemplate",
                                subModuleCode: "EmailTemplate",
                                refCode: "COMPANYSETUP_DELETE"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            EmailTemplateDataService.CommonDeleteData(objectPostString).then(async function (response) {
                                if (response.status == 200) {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                    await vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                                }

                            },);
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
            //     }
            //     else
            //     {
            //         vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
            //         return false;
            //     }
            // })
        },
        UpdateStatus: async function (status) {
            var vm = this;
            if (status == 'Active') {
                status = 1001;
            } else {
                status = 1002;
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    // Send IDs as a comma-separated string
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "COMPANYSETUP_UPDATE",
                        Module: "EmailTemplate",
                        Submodule: "EmailTemplate",
                    };

                    var objectPostString = JSON.stringify(postJSON);
                    console.log(objectPostString);
                    EmailTemplateDataService.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    });
                });
            }
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: function (row, column) {            
            var vm=this;
            if (column.COLUMN_NAME == 'template_name') {
         
                if(row.template_type==true){                 
                    vm.Item=row;                   
                    vm.showEmailTemplate = true;                  
                    vm.ShowAddAdvancedEmailTemplate=false;
                    vm.isShowList=false;   

                }else{
                    vm.Item=row;
                    vm.Template_id=row.template_id; 
                    vm.ShowAddAdvancedEmailTemplate=true;                    
                    vm.showEmailTemplate=false;
                    vm.isShowList=false;              

                }
           }
            if (column.COLUMN_NAME == 'USERNAME') {
                this.$router.push({ name: 'viewcustomreport', params: { ReportID: row.REPORT_ID, ACCESS: row.ACCESS } });
            }
        },
        FetchData: async function () {
            // const params = this.getRequestParams(this.pageNumber, this.pageSize, this.sortBy, this.sortExp, this.isPaged, this.searchCondition);
            var vueObj = this;
            this.isLoading = true;
            // params.pageSize = vueObj.PageSize;
            // params.pageNumber = vueObj.PageNumber;
            // params.searchCondition = vueObj.searchCondition;
            // if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
            // else { vueObj.PageNumber = params.pageNumber = 1; }
            // if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            // else { vueObj.isPaged = params.isPaged = 1; }
            // params.isPaged = vueObj.isPaged;
            // params.sortBy = vueObj.SortBy;
            // params.sortExp = vueObj.SortExp;
            // var srchCondition = "";
            // vueObj.TotalRecords = vueObj.TotalRecords - 1;
            // if (!this.isBlank(this.searchCondition)) { srchCondition = srchCondition + this.searchCondition; }
            // if (!this.isBlank(this.conditionForInstantSearch)) {
            //     srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            // }
            var url=`CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}`;
            await EmailTemplateDataService.EmailTemplateListing(url)
                .then(function (response) {
                    vueObj.Headers = response.data.FieldSchema;
                    const filteredColumnNames = ["template_name", "template_subject", "template_category_id", "template_sub_category_id", "created_at", "created_by", "status_id"];
                    // Filter the original list based on the desired COLUMN_NAME values
                    vueObj.filteredHeaders =  vueObj.Headers.filter(obj => filteredColumnNames.includes(obj.COLUMN_NAME));
                    vueObj.filteredHeaders.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "template_name") {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter,
                            };
                        }                        
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    });
                    // &searchCondition=${vueObj.searchCondition}&isPaged=${vueObj.isPaged}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}
                    if (response.data.Data != "" && response.data.Data != null) {
                        
                        if (response.data.Data.length > 0) {
                            vueObj.EmailTemplateData = response.data.Data;
                            vueObj.EmailTemplateData.forEach(item => {
                                item.template_category_id = JSON.parse(item.template_category_id).Name != null ? JSON.parse(item.template_category_id).Name : "--";  
                                //item.created_by = JSON.parse(item.created_by);
                                item.modified_by = JSON.parse(item.modified_by);
                                item.status_id = JSON.parse(item.status_id);  
                                                                  
                                item.created_by = JSON.parse(item.created_by).Name != null ? JSON.parse(item.created_by).Name : "--";
                                item.template_sub_category_id = JSON.parse(item.template_sub_category_id).Name != null ? JSON.parse(item.template_sub_category_id).Name : "--";
                                
                            });
                            // console.log(vueObj.UserData.map((item) => item.USER_EMP_ID), "EMPID");
                            // vueObj.UserData.forEach(ritem => {
                            //     if (ritem.ACCESS == 1 || ritem.ACCESS == 2) {
                            //         ritem.isCheckBoxDisabled = true;
                            //     } else { ritem.isCheckBoxDisabled = false }
                            //     if (ritem.ACCESS == null) { ritem.ACCESS = 0 }
                            //     ritem.rowDetailsUrl = '';
                            //     // if (ritem.rowDetailsUrl == '') {
                            //     //     // ritem.rowDetailsUrl='/viewcustomreport/' + ritem.REPORT_ID +'/'+ ritem.ACCESS;
                            //     //     ritem.rowDetailsUrl = '/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ritem.REPORT_ID, ACCESS: ritem.ACCESS, Folder_Id: ritem.FOLDER_ID }));
                            //     // }
                            // })
                            vueObj.TotalRecords = vueObj.EmailTemplateData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.EmailTemplateData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }
                    }
                    // if (response.data != "" && response.data != null) {
                    //     if (response.data.data.length > 0) {
                    //         vueObj.ReportData = response.data.data;
                    //         vueObj.ReportData.forEach(ritem => {
                    //             if (ritem.ACCESS == 1 || ritem.ACCESS == 2) {
                    //                 ritem.isCheckBoxDisabled = true;
                    //             } else { ritem.isCheckBoxDisabled = false }
                    //             if (ritem.ACCESS == null) { ritem.ACCESS = 0 }
                    //             ritem.rowDetailsUrl = '';
                    //             if (ritem.rowDetailsUrl == '') {
                    //                 // ritem.rowDetailsUrl='/viewcustomreport/' + ritem.REPORT_ID +'/'+ ritem.ACCESS;
                    //                 ritem.rowDetailsUrl = '/Report/viewcustomreport/' + btoa(JSON.stringify({ ReportID: ritem.REPORT_ID, ACCESS: ritem.ACCESS, Folder_Id: ritem.FOLDER_ID }));
                    //             }
                    //         })
                    //         vueObj.TotalRecords = response.data.data[0].TOTAL_RECORDS;
                    //         vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                    //         vueObj.CurrentPage = vueObj.PageNumber;
                    //         vueObj.noRecord = false;
                    //     } else {
                    //         vueObj.ReportData = [];
                    //         vueObj.noRecord = true;
                    //         vueObj.TotalRecords = 0
                    //     }
                    //     if (localStorage.SavedParameters != undefined) {
                    //         localStorage.removeItem("SavedParameters");
                    //     }
                    // }
                    else {
                        vueObj.noRecord = true;
                        vueObj.EmailTemplateData = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
            });
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                EmailTemplateDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
    }
}
</script>